.album{
    border: 1px solid grey;
    border-radius: 5px;
    height: 80px;
    padding-top: 20px;
    text-align: center;
    margin: 5px;
}

.album:hover{
    cursor: pointer;
}

.indicator{
    
}

.indicatorText{
    display: inline-block;
    font-size: small;
    margin-left: 5px;
}

.redDot{
    background-color: red;
    border: 1px solid;
    border-color: red;
    height: 10px;
    width: 10px;
    border-radius:5px ;
    display: inline-block;
}

.greenDot{
    background-color: green;
    border: 1px solid;
    border-color: green;
    height: 10px;
    width: 10px;
    border-radius:5px ;
    display: inline-block;
}