.post{
    max-width: 60%;
    margin: 10px;
}
.avatar{
    border-radius: 50%;
    width: 50px;
    height: 50px;
    vertical-align: middle;
}
.card-header img{
    float: left;
}
.card-header{
    margin: 0px;
}
.card-body img{
    border-bottom: 1px solid grey;
}
.card-body{
    padding: 5px;
}
.posthead-left-text{
    float: left;
}
.posthead-left-text p{
    margin-bottom: 0px;
    margin-left: 5px;
}
.posthead-right-text{
    float: right;
}
.posthead-right-text p{
    margin-bottom: 0px;
    margin-left: 5px;
}
.myfooter{
    padding-left: 0%;
    padding-right: 0%;
}