.App{
    background-color: #fafafa;
}

@keyframes alertAnimation {
    from {opacity: 1;}
    to {opacity: 0;}
  }

.alertMe {
    opacity: 0;
    position: absolute;
    bottom:10%;
    left:10%;
    z-index: 10;
    width: 80%;
  }

.alertMeShow{
    animation-name: alertAnimation;
    animation-duration: 5s;
}